import React from "react";
import './ServiceCard.css'


const ServiceCard = (props) => {
return(
	<div className='ServiceCard-container' >
     <img className='ServiceCard-img' src={props.photo} alt='img' />
     <p className='ServiceCard-title'> {props.title} </p>
     <p className='ServiceCard-text'> {props.description} </p>
	</div>
	)
}

export default ServiceCard