import React from 'react';
import './Links.css';
import {Switch, Route, Link} from 'react-router-dom';

function Links(props) {
	let passState = props.passState;    
	let classChange = 'Links';
	if (passState === false) {         
		classChange = 'Linksclose'
	}
	
		
  return (
 <div className='nav-main-container'>


    <div className={classChange}  >
      <ul className='nav-links'>
      <li> <Link className='nav-li-a' to='/' > HOME </Link> </li>
      <li> <Link className='nav-li-a' to='/about' > ABOUT </Link> </li>
      <li> <Link className='nav-li-a' to='/contact' > CONTACT </Link> </li>

      </ul>
    </div>
  </div>
  );
}

export default Links;
