import React from "react";
import './MyForm.css';

export default class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
    	<div className='Form-container'>
      

       <div className='Form-container-title'>
       <h1> Request a Quote</h1>
       </div>


      <form   className='Form-container-Form'
        onSubmit={this.submitForm}
        action="https://formspree.io/mnqgqvog"
        method="POST"
      >
        <h1 className='Form-container-response'> Get a response in minutes</h1>
        <label>Email:</label>
        <input  className='Form-email-input' type="email" name="email" placeholder='email' />
        <label>Message:</label>
        <input className='Form-message-input' type="text" name="message"  />
        {status === "SUCCESS" ? <p>Thanks!</p> : <button className='Form-button-input' >Submit</button>}
        {status === "ERROR" && <p>Ooops! There was an error.</p>}
      </form>

         </div>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}