import React from "react";
import logo from '../photos/logo.png'
import './Services.css'
import ServiceCard from './ServiceCard';
import {servicelist} from './Servicelist';

const Services = (props) => {
     const mapServiceCard = servicelist.map((user,i)=>{
          return <ServiceCard key={user[i]} title={servicelist[i].title} description={servicelist[i].description} photo={servicelist[i].photo} /> 
     })
return(
	<div className='Services-Container'>
     <img  src={logo} className='Services-img' />
     <h1 className='Services-title' > Services we provide </h1>
       <div className='Services-cardcontainer'> 
           {mapServiceCard}
       </div>
	</div>
	)
}

export default Services