import React from "react";
import './Top.css'
import logo from '../photos/logo.png'

const Top = (props) => {
return(
	<div className='Top-container'>
         <img   src={logo} className='Top-img' />
        <p className='Top-Text'> <strong>CALL NOW</strong> : +31639222592</p>
	</div>
	)
}

export default Top