import React from 'react';
import foto1 from '.././photos/cover.jpg';
import './Slide.css';
import {Switch, Route, Link} from 'react-router-dom';

const Slide = () => {
	return(
<div className='slide-container'>
   <img className='slide-img' src={foto1} />
  
   <div className='slide-text'>
      <h1><strong className='slide-bold'>ROTTERDAM </strong> PROFESIONAL MAN AND VAN SERVICES</h1>
   </div>
   <div className='slide-button'>
      <Link  to='/quote' >  <button className='slide-button-style'> Request a quote </button> </Link>
   </div>
</div>
);
}

export default Slide;